import * as apiTypes from '../../types/data-item-types'
import { PipelineStage } from './stages'

/**
 * @builder
 */
export interface UnwindStage extends PipelineStage {}

export class UnwindStageImpl implements UnwindStage {
  constructor(private arrayFieldName: string) {}

  /** @private */
  build(): apiTypes.Stage {
    return { unwindArray: { arrayFieldName: this.arrayFieldName } }
  }
}
